@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
.products{
    display: flex;
    justify-content: center;
    margin-top: 15px;
    width: 100%;
    flex-wrap: wrap;
}
.card-display{
    width: 1000px;
    background-color: transparent;
    /* margin: 0px 5px 5px 5px; */
    margin-right: 10px;
    margin-left: 10px;
}
p{
    font-size: 35px;
}
.welcome{
    height: 40vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.newhope{
    height: 20vh;
}
svg text {
    font-family: Lora;
    letter-spacing: 11px;
    stroke: #994d05;
    font-size: 150px;
    font-weight: 700;
    stroke-width: 3;
    animation: textAnimate 5s infinite alternate;
    
  }

  
  @keyframes textAnimate {
    0% {
      stroke-dasharray: 0 50%;
      stroke-dashoffset:  20%;
      fill:hsl(27, 83%, 68%)
  
    }
    
    100% {
      stroke-dasharray: 50% 0;
      stroke-dashoffstet: -20%;
      fill: hsla(189, 68%, 75%,0%)
    }
    
}
.cards{
    height: 62vh;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;

}
.contact{
    margin-left: 500px;
    margin-top: 340px;
}

.footer{
    margin-top: 1rem;
    padding: 1rem;
    background-color:transparent;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
  }

h2{
    font-family: 'Dancing Script', cursive;
}

.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
